@import './src/styles/mixins';
.container {
  width: 100vw;
  height: calc(100vh - 62px);

  .wrapper {
    position: relative;

    display: flex;
    justify-content: center;

    width: 70%;
    height: 100%;
    margin: 0 auto;
    padding: 60px;

    .linkBackward {
      cursor: pointer;

      position: absolute;
      top: 30px;
      left: 0;

      display: flex;
      gap: 12px;
      align-items: center;

      transition: all 0.3s ease-in-out;
      &:hover {
        transform: translateX(-5px);
      }
      .arrow {
        width: 14px;
        height: 9px;
      }

      .backwardText {
        font-size: 14px;
        font-weight: 400;
        line-height: 24px;
      }
    }
    .profileContainer {
      display: flex;
      flex-direction: column;
      align-items: center;

      width: 413px;
      height: max-content;
      padding: 27px 33px;

      color: #000000;

      background: #ffffff;
      border-radius: 2px;
      box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);

      .title {
        margin-bottom: 30px;

        font-size: 22px;
        font-weight: 600;
        line-height: 27px;
        color: inherit;
      }

      .avatarContainer {
        position: relative;

        width: 96px;
        height: 96px;
        margin-bottom: 17px;

        border-radius: 50%;

        .decoration {

          @include flexCenter;

          cursor: pointer;

          position: absolute;
          z-index: 10;
          right: 0;
          bottom: 0;

          width: 32px;
          height: 32px;

          background: #808080;
          border: 1px solid #ffffff;
          border-radius: 50%;

          .cameraIcon {
            width: 17px;
            height: 17px;
          }
        }
      }

      .emailText {
        margin-bottom: 29px;

        font-size: 14px;
        font-weight: 400;
        line-height: 24px;
        color: inherit;

        opacity: 0.5;
      }

      .logOut {
        display: flex;
        gap: 10px;
        align-items: center;

        padding: 8px 20px;

        background: #fcfcfc;
        border-radius: 30px;
        box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);

        transition: all 0.2s ease-in-out;

        &:hover {
          transform: translateY(-1px);
        }

        &:active {
          transform: translateY(1px);
        }

        .logOutIcon {
          width: 16px;
          height: 16px;
        }

        .logOutText {
          cursor: pointer;

          font-size: 16px;
          font-weight: 500;
          line-height: 20px;
          color: #000;
          text-decoration: none;
          letter-spacing: 0.01em;

          opacity: 0.8;

          text-underline: none;
        }
      }
    }
  }
}
