@import './src/styles/mixins';
.btn {
  @include btn;

  cursor: pointer;

  padding: 8px 0;

  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  color: #ffffff;

  background: #366eff;
  border: none;
  border-radius: 30px;
  box-shadow: 0 4px 18px rgba(54, 110, 255, 0.35), inset 0 1px 0 rgba(255, 255, 255, 0.3);
}
